<template>
	<div class="section">
		<section-one :dataOne="aboutData"></section-one>
	</div>
</template>

<script>
	import sectionOne from '../common/section-one.vue';
	export default {
		components: {
			sectionOne
		},
		data() {
			return {
				aboutData: []
			}
		},
		created () {
	  	this.$axios.get('/api/aboutData').then(res => {   //请求接口webName数据
				this.aboutData = res.data.data;
				console.log(this.aboutData);
			}).catch(err => {
				console.log(err);
			});
	  },
		computed: {
		
		}
	}
</script>

<style>
	.section{
		width: 100%;
	}
</style>


